<template>
  <div class="cloud" v-if="hotTagData.length > 0">
    <h2 class="hometitle"></h2>
    <ul>
      <a v-for="item in hotTagData" :key="item.uid" href="javascript:void(0);"
         @click="goToList(item.uid)">{{ item.name }}</a>
    </ul>
  </div>
</template>

<script>
import {getHotTag} from '../../api/index'

export default {
  name: 'TagCloud',
  data () {
    return {
      hotTagData: [] // 
    }
  },
  created () {
    getHotTag().then(response => {
      if (response.data.code === this.$ECode.SUCCESS) {
        this.hotTagData = response.data.records
        console.log(this.hotTagData)
      }
    }).catch(error => {
      console.log(error)
      for (let i = 0; i < 5; ++i) {
        this.hotTagData.push({uid: '1', name: ''})
      }
    })
  },
  methods: {
    // 
    goToList (uid) {
      let routeData = this.$router.resolve({path: '/list', query: {tagUid: uid}})
      window.open(routeData.href, '_blank')
    }
  }
}
</script>
<style>
</style>



